    import FrappeService from "../services/frappe_service";

    export default {
        props: {
            license: Object
        },
        data() {
            return {
                toggle: false
            }
        },

        methods: {

            print_address() {
								var address = {}
								var print_address = [];
                if(this.license.zip_code != "") {
                  address["zip"] = this.license.zip_code;
                }

               	if(this.license.city != "") {
                	address["city"] = this.license.city;
                }

              	if(this.license.county != "") {
                  address["county"] = this.license.county;
								}
								$.each(address, function (name, value) {
									print_address.push(String(value));
								});
								return (print_address.join(' | '));
            },

            quick_entry(doctype, fieldMap) {
                const mapper = {};
                for(let customerField in fieldMap) {
                    let licenseField = fieldMap[customerField];
                    mapper[customerField] = this.license[licenseField];
                }

                frappe.new_doc("Customer", false, function(dialog) {
                    dialog.set_values(mapper);
                });
            },

            make_compliance_info: function(callee) {
                const complianceInfo = {
                    "doctype": "Compliance Info",
                    "license_number": this.license.license_number
                }
                return FrappeService.insertDoc({
                    doc: complianceInfo,
                    isFieldExist: "license_number"
                 }).then(res => {
                     callee();
                }).catch(err => {
                    callee();
                });
            },

            make_customer() {
                const fieldMap = {
                    customer_name: "legal_name",
                    email_id: "email_id",
                    mobile_no: "phone",
                    license: "license_number"
			    };

                this.quick_entry("Customer", fieldMap);
            },

            make_lead() {
                const fieldMap = {
                    customer_name: "legal_name",
                    email_id: "email_id",
                    mobile_no: "phone",
                    license: "license_number"
			    };

                this.quick_entry("Lead", fieldMap);
            },

            make_supplier() {
                const fieldMap = {
                    customer_name: "legal_name",
                    email_id: "email_id",
                    mobile_no: "phone",
                    license: "license_number"
			    };

                this.quick_entry("Supplier", fieldMap);
            }
        }
    }
